import React from 'react';
import ReactDOM from 'react-dom';
import { createStore, Provider } from 'no-redux';
import { actionsData } from './actions';
import './index.css';
import App from './App';

ReactDOM.render(
  <Provider store={createStore(actionsData)}>
    < App />
  </Provider>,
  document.getElementById('root')
);



